<template>
	<view class="content">

		<image class="logo" src="/static/logo.png"></image>
		<view class="text-area">

			<text class="title">{{title}}</text>

		</view>
		<button class="rec-btn" :type="recBtnType" @click="uploadRecord">{{recText}}</button>


	</view>
</template>

<script>
	// import permision from "@/js_sdk/wa-permission/permission.js"
	// const recorderManager = uni.getRecorderManager();
	// const innerAudioContext = uni.createInnerAudioContext();
	import Recorder from 'recorder-core'
	//需要使用到的音频格式编码引擎的js文件统统加载进来
	import 'recorder-core/src/engine/mp3'
	import 'recorder-core/src/engine/mp3-engine'
	import axios from 'axios';

	const API_KEY = "HOTNM8yInQYICDTcwmPyUeWu"
	const SECRET_KEY = "vs2gXbgbOShQ60qfASbHfzA5gkNj9ysq"

	export default {
		data() {
			return {
				recText: '按住说话',
				title: '语音识别DEMO',
				recBtnType: 'success',
				voicetext: "",
				msg: "",
				voicepath: "",
				typeis: "",
				bitRate: "",
				sampleRate: "",
				touchStart: "",
				duration: "",
				powerLevel: ""
			}
		},
		onLoad() {


		},
		methods: {
			//开始录音的方法
			uploadRecord() {
				// 开始录音
				var This = this;
				This.touchStart = true;
				var rec = this.rec = Recorder({
					// type: This.typeis,
					bitRate: 16,
					sampleRate: 16000,
					onProcess: function(buffers, powerLevel, duration, sampleRate) {
						This.duration = duration;
						This.powerLevel = powerLevel;
					}
				});
				rec.open(function() {
					uni.showToast({
						title: '开始录音',
						icon: 'none'
					})
					console.log('开始录音')
					This.rec.start();

					This.reclog("已打开:" + This.type + " " + This.sampleRate + "hz " + This.bitRate + "kbps", 2);
				}, function(msg, isUserNotAllow) {
					// This.dialogCancel();
					console.log(msg, isUserNotAllow)
					This.reclog((isUserNotAllow ? "UserNotAllow，" : "") + "打开失败：" + msg, 1);
				});
				This.waitDialogClickFn = function() {
					// This.dialogCancel();
					This.reclog("打开失败：权限请求被忽略，用户主动点击的弹窗", 1);
				};

			},
			//结束录音的方法
			h5btn_sub_stop() {
				console.log(123123)
				var This = this;
				This.touchStart = false;
				var rec = This.rec;
				This.rec = null;
				if (!rec) {
					This.reclog("未打开录音", 1);
					wx.showToast({
						title: '未开始录音'
					})
					return;
				}
				rec.stop(function(blob, duration) {
					var reader = new FileReader();
					reader.addEventListener("load", function() {
						console.log(reader)
						if (reader.result.length > 100) {
							This.he_show = true
						}
					}, false);
					reader.readAsDataURL(blob);
					console.log(blob, duration, 'blob')
					let blobURL = window.URL.createObjectURL(blob)
					console.log(blobURL, '--------')
					This.iat(blob)

				}, function(s) {
					This.reclog("结束出错：" + s, 1);
				}, true); //自动close
			},

			async getFileContentAsBase64(file, urlEncode) {
				const reader = new FileReader();
				reader.readAsDataURL(file);
				return new Promise((resolve, reject) => {
					reader.onload = () => {
						let base64 = reader.result.split(',')[1];
						if (urlEncode) {
							base64 = encodeURIComponent(base64);
						}
						resolve(base64);
					};
					reader.onerror = error => reject(error);
				});
			},
			//百度语音识别鉴权接口
			async getAccessToken() {

				const url = 'https://aip.baidubce.com/oauth/2.0/token';
				const data = new URLSearchParams();
				data.append('grant_type', 'client_credentials');
				data.append('client_id', API_KEY);
				data.append('client_secret', SECRET_KEY);

				const config = {
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded'
					}
				};

				try {
					const response = await axios.post(url, data, config);
					return response.data.access_token;
				} catch (error) {
					console.error(error);
					// 处理错误情况
				}
			},
			//
			async iat(file) {
				const that = this;
				try {
					const fileContentAsBase64 = await that.getFileContentAsBase64(file, false);
					const mediaType = 'application/json';

					const jsonObject = {
						format: 'pcm',
						rate: 16000,
						channel: 1,
						cuid: 'Kv7DLJH9zl2Hi5iaPQ025LRx13l0kxdo',
						token: await that.getAccessToken(),
						speech: fileContentAsBase64,
						len: file.size
					};

					const content = JSON.stringify(jsonObject);

					const response = await axios.post('https://vop.baidu.com/server_api', content, {
						headers: {
							'Content-Type': 'application/json',
							'Accept': 'application/json'
						}
					});

					const r = response.data;
					console.log(r)
					return r;
				} catch (error) {
					console.error(error);
					// 处理错误情况
				}
			}
		},
	}
</script>

<style>
