		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
		/* flex-direction: column; */
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>